'use strict';

const
  router = require('koa-router'),
  IndexController = require('../index.controller.js');

module.exports = function (app){
  app.use(router(app));
  app.get("/", IndexController.view)
  .get('/api/thing', require('./thing'));

  // app.get('/api/thing', require('./thing'));;
};



