'use strict';

const
  Router = require('koa-router'),
  thingRouter = new Router({
    prefix: "/things"
  }),
  controller = require('./thing.controller');

module.export = function (app){

  thingRouter.get('/', controller.index);
  app.use(thingRouter.routes());
}



